import React from 'react'
import { useContext } from 'react'
import { dataprovider } from './Context'

export default function Profile() {
    const details=useContext(dataprovider)
    
  return (
    <div style={{backgroungdColor:"lightblack",color:"#fff"}}>
        <div>
        {details[0]}
        </div>
        <div className='footer' style={{bottom:"10px",position:"absoulte",width:"100%",height:"40vh",backgroungdColor:"#000"}}>
          <h2>Contact Us :</h2>
        </div>
    </div>
  )
}
